// 方格
import React, { Component } from 'react';
// import PropTypes from 'prop-types';
import { ChessValueSystem } from '../conf/config'

let system = new ChessValueSystem('edu')

class Cube extends Component {
    constructor(props) {
        super(props)
    }
    getClass() {
        let numValue = system.parse(this.props.value)
        let classname = "cube cube-" + numValue + ' cube-position-' + this.props.locate;
        if (this.props.isMerge) {
            classname += ' cube-merged';
        }
        if (this.props.isNew) {
            classname += ' cube-new';
        }
        return classname;
    }
    render() {
        return (
        <div className={this.getClass()}>
            <div className="cube-inner">
                {this.props.value}
            </div>
        </div>)
    }

    componentDidMount() {

    }
}

// Cube.prototype = {
//     isMerge: PropTypes.bool,
//     value: PropTypes.string,  // 幼儿园
//     locate: PropTypes.string  // '1-1'
// }

export default Cube;


